.btn_add__style {
  background-color: #4caf50;
  border: none;
  border-radius: 40px 10px 0 0;
  color: white;
  padding: 1ch 2ch;
  text-decoration: none;
  display: inline-block;
  font-size: 1.5ch;
  cursor: pointer;
  box-shadow: 0 0 10px rgb(161, 161, 161);
}
.btn_add__style:hover {
  color: white;
  background-color: #89e08c;
  box-shadow: 0 0 20px rgb(161, 161, 161);
}
.centering {
  color: white;
  font-size: 18px;
  margin: 0 auto;
  display: block;
  margin-top: 30px;
  margin-bottom: 30px;
  padding: 10px 120px;
}
.name_plate__style {
  background-color: #151617;
  border: none;
  width: 150px;
  text-align: center;
  border-radius: 10px 30px 0 0;
  color: white;
  font-weight: bold;
  padding: 0.5ch 1.5ch 0.5ch 1.5ch;
  font-size: 1.6ch;
  float: left;
}
@media only screen and (max-width: 600px) {
  .btn_add__style {
    font-size: 1.5ch;
    margin-left: 0rem;
    padding: 0.5ch 2.5ch;
  }
}
